.thumbnail {
  background-color: white;
  height: 400px;
  width: 70%;
  margin: 30px 80px;
}

h3 {
  text-align: center;
}

.thumbnail:hover {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.3);
  transform: scale(1.3);
}



